/* Common 동일 변경 요소 */
a{
    text-decoration: none;
  }
  body{
    color:#333;
    font-size:16px;
    font-weight:400;
    line-height:1.4;
    font-family:'Nanum Gothic', sans-serif;
  
  }
  
  img{
    display:block;
  }
  .inner{
    width:1100px;
    margin:0 auto;
    position:relative;
  }
  .btn{
    width:130px;
    padding:10px;
    border: 2px solid #333;
    border-radius: 4px;
    color:#333;
    font-size:16px;
    font-weight:700;
    text-align:center;
    cursor:pointer;
    box-sizing:border-box;
    display: block;
    transition:.4s;
  }
  .btn:hover{
    background-color: #333;
    color:#fff;
  }
  .btn.btn--reverse{
    background-color: #333;
    color:#fff;
  }
  
  .btn.btn--reverse:hover{
    background-color: transparent;
    color:#333;
  }
  
  .btn.btn--brown{
    color:#592b18;
    border-color:#592b18;
  }
  
  
  .btn.btn--brown:hover{
    color:#fff;
    background-color:#582510;
  }
  
  .btn.btn--white{
    color:#fff;
    border-color:#fff;
  }
  
  
  .btn.btn--white:hover{
    color:#333;
    background-color:#fff;
  }
  
  
  .btn.btn--gold{
    color:#d9aa8a;
    border-color:#d9aa8a;
  }
  
  .btn.btn--gold:hover{
    color:#fff;
    background-color:#d9aa9a;
  }
  
  .back-to-position{
    opacity:0;
    transition:1s;
  }
  
  .back-to-position.to-right{
    transform:translateX(-150px);
  
  
  }
  
  .back-to-position.to-left{
    transform:translateX(150px);
  }
  
  
  .show .back-to-position{
    opacity: 1;
    transform:translateX(0);
    
  }
  
  
  .show .back-to-position.delay-0{
    transition-delay: 0s;
  }
  
  
  .show .back-to-position.delay-1{
    transition-delay: .3s;
  }
  .show .back-to-position.delay-2{
    transition-delay: .6s;
  }
  .show .back-to-position.delay-3{
    transition-delay: .9s;
  }
  /* COMMON END 동일 변경 요소 */
  
  /* header */
  header {
    width:100%;;
   background-color:#f6f5f0;
   border-bottom: 1px solid #c8c8c8;
   position:fixed;
   top:0;
   z-index:9;
  }
  
  header > .inner{
    height:120px;
    
  }
  
  header .logo{
    height:75px;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    margin:auto;
  
  }
  
  
  
  /* header-sub */
  
  header .sub-menu{
   display:flex;
   position:absolute;
   top:10px;
   right:0;
  
  }
  
  header .sub-menu ul.menu{
   display:flex;
   font-family:Arial,sans-serif;
  }
  
  
  header .sub-menu ul.menu li {
  
  position:relative;
  }
  
  header .sub-menu ul.menu li::before {
    content:"";
    width:1px;
    height:12px;
    background-color:#e5e5e5;
    position:absolute; 
    top:0;
    bottom:0;
    margin:auto;
  }
  
  header .sub-menu ul.menu li:first-child:before {
    display:none;
  }
  
  header .sub-menu ul.menu li a{
   
    font-size:12px;
    padding:11px 16px;
    display:block;
    color:#656565;
    
  }
  
  header .sub-menu ul.menu li a:hover{
    color:#000;
    
  }
  
  
  header .sub-menu .search {
    
    height:34px;
    position:relative;
  }
  
  header .sub-menu .search input {
    width:36px;
    height:34px;
    padding:4px 10px;
    border:1px solid #ccc;
    box-sizing: border-box;
    border-radius: 5px;
    outline:none;
    background-color:#fff;
    color:#777;
    transition:.9s;
  }
  
  header .sub-menu .search input:focus {
    width:190px;
    border-color:#669900;
  }
  
  
  header .sub-menu .search .material-icons {
    position:absolute;
    height:24px;
    top:0;
    bottom:0;
    right:5px;
    margin:auto;
    transition: .4s;
  }
  
  
  
  header .sub-menu .search.focused .material-icons {
   opacity: 0;
  }
  
  /* header-sub-end */
  
  /* header-main-menu */
  
  header .main-menu{
    position:absolute;
    bottom:0; 
    right:0;
    z-index:1;
    display:flex;
  
  }
  
  
  header .main-menu .item{
    
  }
  
  
  header .main-menu .item .item__name{
    padding:10px 20px 34px 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-size:13px; 
  }
  
  header .main-menu .item:hover .item__name{
    background-color: #2c2a29;
    color:#668800; 
    border-radius:6px 6px 0 0;
  }
  
  
  
  header .main-menu .item .item__contents{
    width:100%;
    position:fixed;
    left:0;
    display:none;
  }
  
  header .main-menu .item:hover .item__contents{
    display:block;
  }
  
  
  header .main-menu .item .item__contents .contents__menu{
    background-color: #2c2a29;
    
  }
  
  
  
  /* header-contents-menu */
  header .main-menu .item .item__contents .contents__menu > ul{
    display: flex;
    padding:20px 0;
  }
  
  header .main-menu .item .item__contents .contents__menu > ul >li{
    width:220px;
  }
  
  
  header .main-menu .item .item__contents .contents__menu > ul >li h4{
    padding: 3px 0 12px 0; 
    font-size:14px;
    color:#fff;
  }
  
  
  header .main-menu .item .item__contents .contents__menu > ul >li ul li{
    padding: 5px 0;
    font-size:12px;
    color:#999; 
    cursor:pointer;
  }
  
  header .main-menu .item .item__contents .contents__menu > ul >li ul li:hover{
    color:#669900;
  }
  
  /* header-contents-menu */
  
  /* header-contents-texture */
  
  
  header .main-menu .item .item__contents .contents__texture{
    padding:26px 0;
    font-size:12px;
    background-image: url("../images/main_menu_pattern.jpg");
  }
  
  header .main-menu .item .item__contents .contents__texture h4{
    color:rgb(216, 216, 216);
    font-weight: 700px;
  }
  
  
  header .main-menu .item .item__contents .contents__texture p{
    color:#669900;
    margin:4px 0 14px;
  }
  
  /* header-contents-texture-end */
  


/* footer-slide */

.awards{
    background-color:#272727;
  }
  
  .awards .inner{
    padding:40px 0;
  }
  
  .awards .swiper-container{
    width:100%;
    height:40px;
  }
  
  .awards .swiper-prev,
  .awards .swiper-next {
    width:42px;
    height:42px;
    border:2px solid #fff;
    border-radius: 50%;
    outline:none;
    color:#fff;
    opacity:.3;
    cursor: pointer;
    display:flex;
    justify-content: center;
    align-items: center;
    position:absolute;
    top:0;
    bottom:0;
    margin:auto;
  }
  
  
  .awards .swiper-prev{
    left:-100px;
  }
  
  
  .awards .swiper-next{
    right:-100px;
  }
  
  
  .awards .swiper-prev:hover,
  .awards .swiper-next:hover{
    background-color:#fff;
    color:#333;
    opacity: 1;
  
  }
  


  
/* footer */

footer{
    background-color:#272727;
    border-top:1px solid #333;
  }
  
  
  footer .inner{
    padding: 40px 0 60px 0; 
  }
  
  
  footer .menu{
    
    display:flex;
    justify-content: center;
    
    
  }

  footer .menu li{
    position:relative; 
  }
  
  
  footer .menu li::before{
    content:""; 
    width:3px;
    height:3px;
    background-color:#fff;
    opacity: .8;
    border-radius: 50%;
    position:absolute;
    top:0;
    bottom:0;
    right:-1px; 
    margin:auto;
  }
  
  footer .menu li:last-child::before{
    display:none;
  }
  
  footer .menu li a{
    color:#ccc;
    font-size:12px;
    font-weight:700;
    padding:15px;
    display:block;
  }
  
  
  footer .menu li a.green {
    color:#669900;
  }
  
  
  
  footer .btn-group{
    margin-top:20px;
    display:flex; 
    justify-content: center;
  }
  
  
  footer .btn-group .btn{
    font-size:12px;
    margin-right:10px;
  
  }
  
  footer .btn-group .btn:last-child{
    margin-right:0;
  }
  
  
  footer .info{
    margin-top:30px;
    text-align: center;
  }
  
  footer .info span{
    margin-right:20px;
    color: #999;
    font-size:12px; 
  }
  
  footer .info span:last-child{
    margin-right:0;
  }
  
  footer .copyright {
    color:#999;
    font-size:12px;
    text-align:center;
    margin-top:12px;
  }
  
  footer .logo{
    margin:30px auto 0;
  }
  

  /* footer-end */